%center-object {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
}

%decorVertical {
  width: $decor-a-size;
  height: $decor-b-size;
  left: 0;
  right: 0;
  margin: 0 auto;
}

%decorHorizontal {
  width: $decor-b-size;
  height: $decor-a-size;
  top: 0;
  bottom: 0;
  margin: auto;
}
